$main-bg: #fff;
$bg-second: #47d3e5;
$main-color: #4268b3;

$txt-main-color: #000;
$txt-second-color: #8d8d8d;
$txt-white: #fff;

$btn-main-bg: $bg-second;
$btn-main-color: rgb(240, 240, 240);

$header-height: 170px;
$header-tablet-height: 70px;
$header-mobile-height: 40px;
$header-shrink-height: 70px;

$box-shadow: rgba(149, 157, 165, 0.2) 0 8px 24px;

$colors: (
    "blue": #4267b2,
    "orange": #faad4f,
    "pink": #f6ada6,
    "white": #fff,
    "black": #000,
    "main": $main-color,
    "red": #ff0000,
);

$mobile-width: 600px;
$tablet-width: 1024px;

$numbers: (0, 1, 2, 3, 4, 5, 6, 7, 8);